/* eslint-disable react-hooks/exhaustive-deps */
import React, { FunctionComponent, useState, useEffect } from "react";
import Particles from "react-particles-js";
import UIfx from "uifx";

import Timer from "../components/Timer";
import Fader from "../components/Fader";

import "./IndexPage.scss";
import planetImg from "../img/planet.png";
import planeImg from "../img/plane_side.png";
const audiofile = require("../audio/takeoff.mp3");
const musicfile = require("../audio/pianosong.mp3");

const IndexPage: FunctionComponent = () => {
  const [faderClass, setFaderClass] = useState("Fader");
  const [entered, setEntered] = useState(false);
  const [pageLoadFinish, setPageLoadFinish] = useState(false);
  const [showMessage, setshowMessage] = useState(false);

  const [planeSFX] = useState(
    new UIfx(audiofile, {
      volume: 0.2,
      throttleMs: 100,
    })
  );

  // Timed Animations
  useEffect(() => {
    if (entered === true) {
      // Fader animations
      setFaderClass("Fader fading");
      const timer = setTimeout(() => setFaderClass("Fader revealing"), 7000);
      const timer2 = setTimeout(() => setFaderClass("Fader revealed"), 10000);

      // Sound
      const timer3 = setTimeout(() => planeSFX.play(), 1000);
      const timer4 = setTimeout(() => setPageLoadFinish(true), 15000);

      //Message
      const timer5 = setTimeout(() => setshowMessage(true), 25000);

      return () => {
        clearTimeout(timer);
        clearTimeout(timer2);
        clearTimeout(timer3);
        clearTimeout(timer4);
        clearTimeout(timer5);
      };
    }
  }, [entered]);

  return (
    <div className="App">
      <Fader
        faderClass={faderClass}
        entered={entered}
        setEntered={setEntered}
      />
      <div className="MainBody">
        <div className="topComponents">
          <Timer displayMessage={showMessage} />
        </div>
        <div className="bottomComponents">
          <img
            src={planetImg}
            alt=""
            className={pageLoadFinish ? "moveIn" : ""}
          />
        </div>
        <div id="plane">
          <img src={planeImg} alt="" />
          <Particles
            width="100%"
            height="100%"
            className="particlesContainer"
            params={{
              particles: {
                number: {
                  value: 10,
                  density: {
                    enable: true,
                    value_area: 10,
                  },
                },
                size: {
                  value: 3,
                },
                move: {
                  direction: "left",
                  out_mode: "out",
                },
                line_linked: {
                  enable: false,
                },
              },
            }}
          />
        </div>
      </div>
      {pageLoadFinish && (
        <audio autoPlay loop>
          <source src={musicfile} type="audio/mp3" />
        </audio>
      )}
    </div>
  );
};

export default IndexPage;
