/* eslint-disable react-hooks/exhaustive-deps */
import React, { FunctionComponent, useState, useEffect } from "react";
import "./Fader.scss";
import ticketImg from "../img/convite.png";

interface ComponentProps {
  faderClass: string;
  entered: boolean;
  setEntered(val: boolean): void;
}

const FaderComponent: FunctionComponent<ComponentProps> = ({
  faderClass,
  entered,
  setEntered,
}) => {
  const [ticketClass, setTicketClass] = useState("ticketImage");

  // Ticket Fade
  useEffect(() => {
    const timer = setTimeout(() => {
      setTicketClass("ticketImage revealing");
      clearTimeout(timer);
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <div className={faderClass}>
      {!entered && (
        <img
          className={ticketClass}
          alt=""
          src={ticketImg}
          onClick={() => setEntered(true)}
        />
      )}
      {entered && <img src="" alt="" id="plane" />}
    </div>
  );
};

export default FaderComponent;
